.navbar {
  background-color: $autumn;
  opacity: 0.95;
  width: 60vw;
  position: fixed;
  right: 0;
  height: 100%;
  top: 0;
  z-index: 10;

  &__list {
    color: $light-autumn;
    font-size: 1em;
    font-family: $font-poppins;
    font-weight: 700;
    letter-spacing: 2px;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 1em;
    position: absolute;
    top: 20%;
    right: 20%;

    &__link {
      color: $light-autumn;
      font-family: $font-poppins;
      font-size: 1.2em;
      letter-spacing: 1.5px;
      padding: 0.5em;
      text-align: center;
      text-decoration: none;
    }

    &__link:hover {
      cursor: pointer;
      font-size: 1.3em;
      font-weight: 600;
      color: $coolgray;
    }
  }
  &__dash{
    color: $coolgray;
    padding-right: 0.5em;

  }
}

.fa-xmark {
  position: absolute;
  padding: 10px;

  right: 8.5vw;
  top: 8.5vh;
  color: $light-autumn;
}

.fa-xmark:hover {
  cursor: pointer;
  color: $light-autumn;
}
